import React, { useMemo } from 'react';
import { Redirect, useParams } from 'react-router';
import { heroImages } from '../../helpers/heroImages';
import { getHerosById } from '../../selectors/getHeroesById';

// import batman from '../../assets/heroes/dc-batman.jpg'; static source

export default function HeroScreen({ history }) {

    const { heroId } = useParams();
    const hero = useMemo(() => getHerosById(heroId), [heroId]);
    if(hero.length === 0){
        return <Redirect to="/"/>
    }
    const handleReturn = () => {
        if(history.length <= 2){
            history.push('/');
        }else {
            history.goBack();
        }
    }
    const [{
        superhero,
        publisher,
        alter_ego,
        first_appearance,
        characters,
    }] = hero;
    return (
        <div className="row mt-5 ">
            <div className="col-4">
                <img 
                    // src={ `../assets/heroes/${heroId}.jpg` } from public/assets
                    // src={batman} from import 
                    src={ heroImages(`./${heroId}.jpg`).default }
                    alt={ superhero }
                    className="img-thumbnail animate__animated animate__fadeInLeft"
                />
            </div>
            <div className="col-8 animate__animated animate__fadeIn">
                <h3> { superhero } </h3>
                <ul className="list-group list-group-flush">
                    <li className="list-group-item"><b>Alter ego: </b> { alter_ego } </li>
                    <li className="list-group-item"><b>Publisher: </b> { publisher } </li>
                    <li className="list-group-item"><b>First appearance: </b> { first_appearance } </li>
                </ul>
                <h5>Character</h5>
                <p>{ characters }</p>
                <button 
                    className="btn btn-outline-info"
                    onClick={ handleReturn }
                >
                    Return
                </button>
            </div>
        </div>
    );
}
